"use client"

import { useState, useEffect } from 'react'
import { useParams } from 'next/navigation'
import Link from 'next/link'
import Navbar from '@/components/Navbar'
import Footer from '@/components/Footer'
import { Button } from '@/components/ui/button'
import { fetchProduct, type Product } from '@/lib/api'

export default function ProductDetailPage() {
  const params = useParams()
  const slug = params.slug as string
  
  const [product, setProduct] = useState<Product | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [quantity, setQuantity] = useState(1)
  const [selectedImage, setSelectedImage] = useState(0)

  useEffect(() => {
    const loadProduct = async () => {
      try {
        const productData = await fetchProduct(slug)
        if (!productData) {
          setError('Produto n√£o encontrado')
        } else {
          setProduct(productData)
        }
      } catch (err) {
        setError(err instanceof Error ? err.message : 'Erro ao carregar produto')
      } finally {
        setLoading(false)
      }
    }

    if (slug) {
      loadProduct()
    }
  }, [slug])

  const formatPrice = (price: number) => {
    return new Intl.NumberFormat('pt-BR', {
      style: 'currency',
      currency: 'BRL'
    }).format(price)
  }

  const handleAddToCart = () => {
    // Here you would typically add the product to cart context/state
    alert(`${product?.name} adicionado ao carrinho! Quantidade: ${quantity}`)
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-white">
        <Navbar />
        <div className="flex justify-center items-center py-20">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-black"></div>
        </div>
        <Footer />
      </div>
    )
  }

  if (error || !product) {
    return (
      <div className="min-h-screen bg-white">
        <Navbar />
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-20 text-center">
          <h1 className="text-2xl font-bold text-gray-900 mb-4">
            {error || 'Produto n√£o encontrado'}
          </h1>
          <p className="text-gray-600 mb-8">
            O produto que voc√™ est√° procurando n√£o existe ou foi removido.
          </p>
          <Link href="/produtos">
            <Button>Voltar aos Produtos</Button>
          </Link>
        </div>
        <Footer />
      </div>
    )
  }

  const discountPercentage = product.originalPrice 
    ? Math.round(((product.originalPrice - product.price) / product.originalPrice) * 100)
    : 0

  // Mock additional images for gallery
  const productImages = [
    product.image,
    product.image.replace('?text=', '?text=Vista+lateral+'),
    product.image.replace('?text=', '?text=Detalhes+'),
    product.image.replace('?text=', '?text=Embalagem+')
  ]

  return (
    <div className="min-h-screen bg-white">
      <Navbar />
      
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Breadcrumb */}
        <nav className="flex mb-8 text-sm">
          <Link href="/" className="text-gray-500 hover:text-gray-700">
            In√≠cio
          </Link>
          <span className="mx-2 text-gray-500">/</span>
          <Link href="/produtos" className="text-gray-500 hover:text-gray-700">
            Produtos
          </Link>
          <span className="mx-2 text-gray-500">/</span>
          <span className="text-gray-900">{product.name}</span>
        </nav>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-12">
          {/* Product Images */}
          <div className="space-y-4">
            <div className="aspect-square overflow-hidden rounded-lg bg-gray-100">
              <img
                src={productImages[selectedImage]}
                alt={product.name}
                className="w-full h-full object-cover"
                onError={(e) => {
                  e.currentTarget.src = "https://placehold.co/600x600?text=Produto+Indispon√≠vel"
                }}
              />
            </div>
            
            {/* Image Gallery */}
            <div className="grid grid-cols-4 gap-2">
              {productImages.map((image, index) => (
                <button
                  key={index}
                  onClick={() => setSelectedImage(index)}
                  className={`aspect-square overflow-hidden rounded-md border-2 ${
                    selectedImage === index ? 'border-black' : 'border-gray-200'
                  }`}
                >
                  <img
                    src={image}
                    alt={`${product.name} - Imagem ${index + 1}`}
                    className="w-full h-full object-cover"
                    onError={(e) => {
                      e.currentTarget.src = "https://placehold.co/150x150?text=Img"
                    }}
                  />
                </button>
              ))}
            </div>
          </div>

          {/* Product Info */}
          <div className="space-y-6">
            <div>
              <div className="flex items-center gap-2 mb-2">
                <span className="bg-gray-100 text-gray-800 px-2 py-1 rounded-md text-sm">
                  {product.category}
                </span>
                {product.inStock ? (
                  <span className="bg-green-100 text-green-800 px-2 py-1 rounded-md text-sm">
                    Em estoque
                  </span>
                ) : (
                  <span className="bg-red-100 text-red-800 px-2 py-1 rounded-md text-sm">
                    Fora de estoque
                  </span>
                )}
              </div>
              
              <h1 className="text-3xl font-bold text-gray-900 mb-4">
                {product.name}
              </h1>
              
              <div className="flex items-center gap-4 mb-6">
                <span className="text-3xl font-bold text-gray-900">
                  {formatPrice(product.price)}
                </span>
                {product.originalPrice && (
                  <>
                    <span className="text-xl text-gray-500 line-through">
                      {formatPrice(product.originalPrice)}
                    </span>
                    <span className="bg-red-500 text-white px-2 py-1 rounded-md text-sm font-semibold">
                      -{discountPercentage}%
                    </span>
                  </>
                )}
              </div>
            </div>

            <div>
              <h3 className="text-lg font-semibold mb-2">Descri√ß√£o</h3>
              <p className="text-gray-600 leading-relaxed">
                {product.description}
              </p>
            </div>

            <div>
              <h3 className="text-lg font-semibold mb-2">Caracter√≠sticas</h3>
              <ul className="space-y-1">
                {product.features.map((feature, index) => (
                  <li key={index} className="flex items-center text-gray-600">
                    <span className="w-2 h-2 bg-black rounded-full mr-3"></span>
                    {feature}
                  </li>
                ))}
              </ul>
            </div>

            {/* Quantity and Add to Cart */}
            <div className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Quantidade
                </label>
                <div className="flex items-center border border-gray-300 rounded-md w-32">
                  <button
                    onClick={() => setQuantity(Math.max(1, quantity - 1))}
                    className="px-3 py-2 text-gray-600 hover:text-gray-800"
                    disabled={quantity <= 1}
                  >
                    -
                  </button>
                  <span className="flex-1 text-center py-2 border-x border-gray-300">
                    {quantity}
                  </span>
                  <button
                    onClick={() => setQuantity(quantity + 1)}
                    className="px-3 py-2 text-gray-600 hover:text-gray-800"
                  >
                    +
                  </button>
                </div>
              </div>

              <div className="flex flex-col sm:flex-row gap-4">
                <Button
                  onClick={handleAddToCart}
                  disabled={!product.inStock}
                  className="flex-1 bg-black hover:bg-gray-800 disabled:bg-gray-300"
                  size="lg"
                >
                  {product.inStock ? 'Adicionar ao Carrinho' : 'Produto Indispon√≠vel'}
                </Button>
                <Button
                  variant="outline"
                  size="lg"
                  className="flex-1"
                >
                  Comprar Agora
                </Button>
              </div>
            </div>

            {/* Trust Signals */}
            <div className="border-t pt-6">
              <div className="grid grid-cols-2 gap-4 text-sm">
                <div className="flex items-center text-gray-600">
                  <span className="w-2 h-2 bg-green-500 rounded-full mr-2"></span>
                  Frete gr√°tis acima de R$ 200
                </div>
                <div className="flex items-center text-gray-600">
                  <span className="w-2 h-2 bg-green-500 rounded-full mr-2"></span>
                  Troca em at√© 30 dias
                </div>
                <div className="flex items-center text-gray-600">
                  <span className="w-2 h-2 bg-green-500 rounded-full mr-2"></span>
                  Pagamento seguro
                </div>
                <div className="flex items-center text-gray-600">
                  <span className="w-2 h-2 bg-green-500 rounded-full mr-2"></span>
                  Garantia de qualidade
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Additional Information */}
        <div className="mt-16 border-t pt-16">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-12">
            <div>
              <h3 className="text-xl font-bold mb-4">Informa√ß√µes de Entrega</h3>
              <div className="space-y-3 text-gray-600">
                <p>‚Ä¢ Entregamos para todo o Brasil</p>
                <p>‚Ä¢ Frete gr√°tis para compras acima de R$ 200</p>
                <p>‚Ä¢ Prazo de entrega: 5 a 10 dias √∫teis</p>
                <p>‚Ä¢ Embalagem especial para presente</p>
              </div>
            </div>
            
            <div>
              <h3 className="text-xl font-bold mb-4">Pol√≠tica de Troca</h3>
              <div className="space-y-3 text-gray-600">
                <p>‚Ä¢ Troca gratuita em at√© 30 dias</p>
                <p>‚Ä¢ Produto deve estar em perfeito estado</p>
                <p>‚Ä¢ Embalagem original preservada</p>
                <p>‚Ä¢ Certificado de autenticidade incluso</p>
              </div>
            </div>
          </div>
        </div>
      </main>

      <Footer />
    </div>
  )
}
